.footer {
    padding: 36px 0;
    margin-top: auto;

&-row,
&-contacts {
    display: flex;
}
&-row{
    margin-bottom: 84px;
    align-items: flex-start;
}
&-contacts{
    align-items: center;
}
&_logo{
    position: relative;
    &:before{
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 371px;
        height: 260px;
        background: url(./assets/img/decoration__footer-logo.png) center/contain no-repeat;
        z-index: -1;
    }
}

&-nav {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 761px;
    width: 100%;
    &__column {
        padding: 0 12px;
    }
    &__title {
    font-size: 20px;
    color: #BBB3B3;
    margin-bottom: 8px;
    font-weight: 400;
}

&__link {
    color: #5b595a;
    font-size: 20px;
    margin-bottom: 8px;
    text-decoration: none;
}

&__item {
    margin-bottom: 8px;
}

&__item:last-child {
    margin-bottom: 0;
}
}



&-social {
    margin-left: auto;
    &__item{
        position: relative;
        display: inline-block;
        &::before{
            content: "";
            position: absolute;
            left: -73px;
            top: -35px;
            width: 168px;
            height: 153px;
            background-image: url(./assets/img/decoration__footer-instagram.png);
            z-index: -1;
        }
    }
}
&-phones{
    display: flex;
    gap: 24px;
&__item {
    color: $secondary-color;
    font-size: 26px;
    font-weight: 600;
    text-decoration: none;
    position: relative;
    &::before{
        content: "";
        position: absolute;
        left: px;
        top: px;
        width: px;
        height: px;
        background-image: url(./assets/img/decoration__footer-phone.png);
        z-index: -1;
    }
}
}
}

